body {
  margin: 0;
  background: whitesmoke; }

header {
  height: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: orange; }
  header p {
    font-family: cursive;
    color: white;
    font-size: 60px;
    font-weight: italic; }

.middleText {
  display: flex;
  text-align: center;
  width: 1500px;
  height: 500px;
  padding-top: 5%; }
  .middleText .meme-form {
    width: 90%;
    align-self: center;
    align-content: center;
    justify-content: center;
    padding-left: 2%; }
    .middleText .meme-form h1 {
      font-size: 50px;
      color: orange; }
    .middleText .meme-form label {
      font-size: 25px;
      display: inline-block;
      clear: left;
      width: 25%;
      text-align: right;
      color: orange;
      font-weight: bold; }
    .middleText .meme-form input {
      width: 65%;
      height: 10%;
      padding: 1%;
      margin: 3%;
      text-align: center;
      display: inline-block;
      font-size: 20px; }
    .middleText .meme-form button {
      border: none;
      font-family: cursive;
      font-size: 25px;
      letter-spacing: 1.5px;
      color: white;
      background: orange;
      padding: 1rem;
      width: 93%; }
  .middleText .meme {
    position: relative;
    width: 100%;
    margin: auto;
    align-self: center;
    align-content: center;
    justify-content: center; }
    .middleText .meme img {
      max-height: 650px;
      max-width: 600px; }
    .middleText .meme h2 {
      position: absolute;
      width: 80%;
      text-align: center;
      left: 50%;
      -webkit-transform: translateX(-50%);
              transform: translateX(-50%);
      margin: 15px 0;
      padding: 0 5px;
      font-family: impact, sans-serif;
      font-size: 2em;
      text-transform: uppercase;
      letter-spacing: 1px;
      text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000, -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000, 2px 2px 5px #000; }
    .middleText .meme .bottom {
      bottom: 0; }
    .middleText .meme .top {
      top: 0; }
  .middleText .colorPicker {
    position: relative;
    width: 100%;
    margin: auto;
    align-self: center;
    align-content: center;
    justify-content: center;
    padding-left: 0; }
    .middleText .colorPicker label {
      font-size: 20px;
      display: inline-block;
      clear: left;
      width: 30%;
      text-align: right;
      color: orange;
      font-weight: bold; }
    .middleText .colorPicker input {
      width: 10%;
      height: 2rem !important;
      padding: 0.5%; }

